import { Component, OnInit } from '@angular/core';
import {environment} from '../../../environments/environment';
declare var $: any;

@Component({
  selector: 'app-photography',
  templateUrl: './photography.component.html',
  styleUrls: ['./photography.component.css']
})
export class PhotographyComponent implements OnInit {

  urls: any = {
    imagesSmall: environment.assetsBase + '/images/photography/sm/',
    imagesLarge: environment.assetsBase + '/images/photography/lg/',
  };

  filename = 'photo-{number}.jpg';
  count = 21;

  photos: any[] = [];
  selectedPhoto: any;

  fullImageView(photo): void {
    this.selectedPhoto = photo;
    $('.ui.dimmer').dimmer('show');
  }

  constructor() { }

  ngOnInit() {
    for (let i = 1; i <= this.count; i++) {
      this.photos.push({
        small: this.urls.imagesSmall + this.filename.replace('{number}', i.toString()),
        large: this.urls.imagesLarge + this.filename.replace('{number}', i.toString())
      });
    }
  }

}
